import Stack from '@mui/material/Stack';
import React, { Component, useEffect } from 'react';
import ReactDOM from 'react-dom';
import Button from '@mui/material/Button';
import { Navigate, useNavigate, useParams } from 'react-router-dom';
import ResponsiveAppBar from './NavBar'
import MainMenu from './MainMenu';
import LoginNew from './LoginNew';
import { selectUser } from "../features/userSlice";
import { useSelector } from 'react-redux';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Divider from '@mui/material/Divider';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Phone } from '@mui/icons-material';
import { useState } from 'react';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';


function Profile({ navigation, route }) {
  

    const noPointer = { cursor: 'default' };

    const [HotelName, setHotelName] = useState('');
    const [Address, setAddress] = useState('');
    const [Phone, setPhone] = useState('');
    const [email, setEmail] = useState('');

    const [backDropOpen, setBackDropOpen] = React.useState(false);

    const [openSuccess, setOpen] = React.useState(false);
    const [openDelete, setDelete] = React.useState(false);
    const [AddressDetails, setAddressDetails] = useState([]);

    useEffect(() => {

        async function getHoteDetails() {


            const userid = localStorage.getItem('LoggedUserID');
            if (localStorage.getItem('LoggedUserID') == null) {
                window.location.href = "/LoginNew";
                return;
            }

            const requestOptions = {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' },
            };

           
            let response = await fetch('https://hotelapi20220307195557.azurewebsites.net/hotelapi/GetHotels?userid=' + userid, requestOptions);

            const result = await response.json();
           
            setAddressDetails(result);
            

        }
        getHoteDetails();

    }, [AddressDetails]);


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpen(false);
        setDelete(false);
    };
    const action = (
        <React.Fragment>
            <IconButton
                size="small"
                aria-label="close"
                color="inherit"
                onClick={handleClose}
            >
                <CloseIcon fontSize="small" />
            </IconButton>
        </React.Fragment>
    );

    async  function  deleteHotel(hotelID) {
        if (window.confirm('Are you sure you want to Delete this Hotel?' + hotelID)) {
            // Save it!
            const requestOptions = {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' },
            };


            let response = await fetch('https://hotelapi20220307195557.azurewebsites.net/hotelapi/DeleteHotel?hotelID=' + hotelID, requestOptions);

            setDelete(true);
            console.log('Hotel deleted');
          } else {
            // Do nothing!
            console.log('Thing was not saved to the database.');
          }
    }
    function editHotel() {
        alert('Editing hotel');
    }

    if (localStorage.getItem('JayToken') == null || localStorage.getItem('LoggedUserName') == null || localStorage.getItem('LoggedUserID') == null) {
        window.location.href = "/LoginNew";
        return;
    }




    async function handleSubmit() {


        setBackDropOpen(true);
        if (localStorage.getItem('LoggedUserID') == null) {
            window.location.href = "/LoginNew";
            return;
        }

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(
                {
                    "HotelName": HotelName,
                    "Address": Address,
                    "Phone": Phone,
                    "Email": email,
                    "UserId": parseInt(localStorage.getItem('LoggedUserID'))
                })
        };

        let result = await fetch('https://hotelapi20220307195557.azurewebsites.net/hotelapi/CreateHotel', requestOptions);
        setOpen(true);
        setBackDropOpen(false);

    }



    return (
        <div >

            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={backDropOpen}
                onClick={handleClose} >
                <CircularProgress color="inherit" />
            </Backdrop>


            <Snackbar
                open={openSuccess}
                autoHideDuration={6000}
                onClose={handleClose}
                action={action} >
                <Alert severity="success">Hotel Information has been Saved !</Alert>
            </Snackbar>

            <Snackbar
                open={openDelete}
                autoHideDuration={6000}
                onClose={handleClose}
                action={action} >
                <Alert severity="success">Hotel Information has been Deleted !</Alert>
            </Snackbar>



            <ResponsiveAppBar loggedinUser={localStorage.getItem('LoggedUserName')} />
            <Stack direction="vertical" gap={5} p={1} >
                <div><h2>Welcome {localStorage.getItem('LoggedUserFirstName')} </h2></div>
            </Stack>

            <Grid container sx={{ height: '100vh' }}>
                <Grid item xs={12} sm={8} md={5} elevation={6} square>
                    <Typography component="h1" variant="h5" sx={{ mt: 1, p: 1 }}>
                        Add Hotel
                    </Typography>

                    <Box component="form" noValidate sx={{ mt: 1, p: 2 }}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}  >
                                <TextField onChange={e => setHotelName(e.target.value)}
                                    autoComplete="hotel-name"
                                    name="hotelName"
                                    required
                                    fullWidth
                                    id="hotelName"
                                    label="Hotel Name"
                                    autoFocus
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <TextField onChange={e => setAddress(e.target.value)}
                                    required
                                    fullWidth
                                    id="address"
                                    label="Address"
                                    name="address"
                                    autoComplete="address"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField onChange={e => setPhone(e.target.value)}
                                    required
                                    fullWidth
                                    name="phone"
                                    label="phone"
                                    type="phone"
                                    id="phone"
                                    autoComplete="phone"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField onChange={e => setEmail(e.target.value)}
                                    required
                                    fullWidth
                                    name="email"
                                    label="email"
                                    type="email"
                                    id="email"
                                    autoComplete="email"
                                />
                            </Grid>
                            <Grid item xs={12}>

                            </Grid>
                        </Grid>
                        <Button
                            onClick={() => handleSubmit()}
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                        >
                            Save
                        </Button>



                    </Box>




                </Grid>

                <Typography component="h1" variant="h5">
                    Hotels
                </Typography>

                <Grid sx={{ overflowY: 'scroll', maxHeight: 400 }} item xs={12} sm={8} p={4} md={5} elevation={5} square>




                    {AddressDetails.map((hotelDetails) => (
                        

                        <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                            <ListItem alignItems="flex-start">
                                <ListItemAvatar>
                                    <Avatar alt={hotelDetails.hotelName} src="/static/images/avatar/1.jpg" />
                                </ListItemAvatar>
                                <ListItemText
                                    primary={hotelDetails.hotelName}
                                    secondary={
                                        <React.Fragment>
                                            <Typography
                                                sx={{ display: 'inline' }}
                                                component="span"
                                                variant="body2"
                                                color="text.primary"
                                            >

                                            </Typography>
                                            {hotelDetails.address}   {hotelDetails.phone}   {hotelDetails.email}


                                        </React.Fragment>
                                    }
                                />
                                <EditIcon onClick={() => editHotel()} style={{ color: 'grey' }} />
                                <DeleteIcon onClick={() => deleteHotel(hotelDetails.id)} style={{ color: 'green' }} />



                            </ListItem>
                            <Divider variant="inset" component="li" />


                        </List>



                    ))}




                </Grid>
            </Grid>




        </div>
    )

}
export default Profile