import Stack from '@mui/material/Stack';
import React, { Component, useEffect } from 'react';
import ReactDOM from 'react-dom';
import Button from '@mui/material/Button';
import { Navigate, useNavigate, useParams } from 'react-router-dom';
import ResponsiveAppBar from './NavBar'
import MainMenu from './MainMenu';
import LoginNew from './LoginNew';
import { selectUser } from "../features/userSlice";
import { useSelector } from 'react-redux';
 
function Home({ navigation, route }) {

  
    if (localStorage.getItem('JayToken') == null || localStorage.getItem('LoggedUserName') == null) {
        window.location.href = "/LoginNew";
        return;
    }

    return (
        <div>
            <ResponsiveAppBar loggedinUser={localStorage.getItem('LoggedUserName')}/>
            <Stack direction="vertical" gap={5} >  
            <MainMenu description={"The daily transactions go here"} ButtonTitle="Transactions" clickEvent={"daily"} />
            <MainMenu description={"Create Hotel serving Tables  here"} ButtonTitle="Create" clickEvent={"table"} />
            <MainMenu description={"Categorize food for hotel here"} ButtonTitle="Create" clickEvent={"food"} />
            </Stack>

          
        </div>
    )

}
export default Home