import * as React from 'react';
import { useDispatch } from 'react-redux';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useEffect, useState } from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import Cookies from 'js-cookie';
import Home from '../components/Home';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import alertContainer from "@material-ui/core/Container";
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
// import Backdrop from './Backdrop';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import { login } from '../features/userSlice';


function LoginNew() {
  const [open, setOpen] = React.useState(false);
  const [openSuccess, setOpenSuccess] = React.useState(false);

  const [backDropOpen, setBackDropOpen] = React.useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  let navigate = useNavigate();
  const dispatch = useDispatch();

  

  if (localStorage.getItem('JayToken') != null && localStorage.getItem('LoggedUserName') != null &&  localStorage.getItem('LoggedUserID')!=null) {

    window.location.href = "/Home";
  }
 
  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };
  const action = (
    <React.Fragment>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </React.Fragment>
  );

  async function SignIn() {

    setBackDropOpen(true);

    if (localStorage.getItem('JayToken') != null && localStorage.getItem('LoggedUserName') != null &&  localStorage.getItem('LoggedUserID')!=null) {

      navigate("/home", { test: 'Hello' });
    }
    else {

    }
    let result = await fetch("https://hotelapi20220307195557.azurewebsites.net/hotelapi/login?username=" + email + "&password=" + password,
      {
        method: "GET",
      });
    result = await result.json();
    setBackDropOpen(false);
    if (result.message == "Invalid Login") {

      setOpen(true);

    }
    else {
      localStorage.setItem('JayToken', JSON.stringify(result.jwtTokenString));
      localStorage.setItem('LoggedUserName', email);
      localStorage.setItem('LoggedUserID', JSON.stringify(result.id));
      localStorage.setItem('LoggedUserFirstName', result.firstName);
      localStorage.setItem('LoggedUserLastName', result.lastName);
      // dispatch(
      //   login({
      //     name:email
      //   })
      // )
      navigate("/home");
    }

  }


  return (
    <div>

      <Container component="main" maxWidth="xs">

        <Backdrop
          sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={backDropOpen}
          onClick={handleClose} >
          <CircularProgress color="inherit" />
        </Backdrop>

        <Snackbar        
          open={openSuccess}
          autoHideDuration={6000}
          onClose={handleClose}
          action={action} >
          <Alert severity="error">user created</Alert>
        </Snackbar>


        <Snackbar
          open={open}
          autoHideDuration={6000}
          onClose={handleClose}
          action={action}
        >

          <Alert severity="error">Invalid credentials ! .Wrong Username or password</Alert>
        </Snackbar>


        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Typography component="h1" variant="h5">
            HotMan Software Services
          </Typography>
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h3" variant="h5">
            Sign in
          </Typography>

          <TextField onChange={e => setEmail(e.target.value)}
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
          />
          <TextField onChange={e => setPassword(e.target.value)}
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
          />
          {/* <FormControlLabel
                      control={<Checkbox value="remember" color="primary" />}
                      label="Remember me"
                    /> */}
          <Button
            onClick={() => SignIn()}
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="/Signup" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>

        </Box>

      </Container>

    </div>
  )
}

export default LoginNew;